.x-404-bg{width: 100%; overflow: hidden;  padding-top: 0; height: 100%; background: url('/static/images/new/x-404-bg.jpg') top center no-repeat #fff; color: #fff; text-align: center; font-size: 24px;line-height: 30px;}
